/* Btn */
button {
	font-size: inherit;
	font-family: inherit;
	outline: none;
	appearance: none;
	border: none;
	background-color: transparent;
	cursor: pointer;
	padding: 0;
}

a {
	text-decoration: none;
}

.btn {
	display: inline-block;
	min-width: 160px;
	font-size: 15px;
	line-height: calc(19.5/15);
	font-weight: 400;
	text-align: center;
	color: $white;
	background-color: $blue-1;
	border-radius: 5px;
	cursor: pointer;
	padding: 13px 24.5px;

	transition: all .2s ease;

	&:not(.disabled):hover, &:not(.disabled):focus-visible {
		background-color: $lblue;
	}

	@media (max-width: 576px) {
		min-width: 130px;
	}

	/* Btn Views */
	&_small {
		min-width: 100px;
		padding: 8px 24.5px;
	}


	&_main {
		position: relative;
		font-size: 17px;
		line-height: calc(22.1/17);
		font-weight: 500;
		overflow: hidden;
		border: 1px solid $lblue;
		box-shadow: inset -2px -2px 0 $lblue, inset 2px 2px 0 $lblue;
		padding: 10.5px 21.5px;

		&::before, &::after {
			content: attr(data-text);
			position: absolute;
			z-index: 1;
			width: 100%;
			height: 50%;
			left: 0;
			background-color: $lblue;
			overflow: hidden;
			-webkit-transition: -webkit-transform 0.3s;
			transition: transform 0.3s;
			-webkit-transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
			transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
		}
		&::before {
			top: 0;
			padding-top: 10.5px;
		}
		&::after {
			bottom: 0;
			line-height: 0;
		}

		& > span {
			display: block;
			-webkit-transform: scale3d(0.2, 0.2, 1);
			transform: scale3d(0.2, 0.2, 1);
			opacity: 0;
			-webkit-transition: -webkit-transform 0.3s, opacity 0.3s;
			transition: transform 0.3s, opacity 0.3s;
			-webkit-transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
			transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
		}

		&:not(.disabled):hover, &:not(.disabled):focus-visible {
			background-color: $blue-1;
			box-shadow: none;

			&::before {
				transform: translate3d(0, -100%, 0);
			}
			&::after {
				transform: translate3d(0, 100%, 0);
			}

			& > span {
				opacity: 1;
				-webkit-transform: scale3d(1, 1, 1);
				transform: scale3d(1, 1, 1);
			}
		}

		@media (max-width: 576px) {
			font-size: 15px;
			line-height: calc(19.5/15);
			padding: 12px 13.5px;

			&::before {
				padding-top: 12px;
			}
		}
	}

	&_tab {
		color: $grey;
		border: 1px solid transparent;
		padding: 10.5px 21.5px;
		&.active {
			color: $white;
			border-color: $lblue;
		}
		
		&:not(.disabled):hover, &:not(.disabled):focus-visible {
			background-color: $blue-1;
			color: $white;
		}

		@media (max-width: 576px) {
			padding: 10.5px 11px;
		}
	}

	&_plain {
		min-width: auto;
		font-size: 13px;
		line-height: calc(16.9/13);
		border: 1px solid transparent;
		padding: 13px 12px;
		
		&:not(.disabled):active {
			background-color: $blue-1;
			border-color: $lblue;
		}
		&:not(.disabled):hover, &:not(.disabled):focus-visible {
			background-color: $blue-1;
		}
	}

	/* Btn states */
	&.disabled {
		color: $grey;
		cursor: default;
	}

	&:not(.disabled).highlighted {
		color: $white;
		background-color: $lblue;
	}
}

.btns {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	gap: 15px;
	
	.btn {
		flex: 1;
	}
}

.tabs {
	display: flex;
	align-items: center;
	gap: 25px;
	margin-bottom: 30px;

	@media (max-width: 576px) {
		gap: 20px;
	}

	@media (max-width: 480px) {
		.btn_tab {
			flex: 1;
		}
	}
}
/* Btn end */

/* Input Text */
input[type="text"] {
	appearance: none;
	outline: none;
	box-shadow: none;

	width: 100%;
	font-size: 15px;
	line-height: calc(19.5/15);
	color: $white;
	background-color: transparent;
	border: 1px solid $grey;
	border-radius: 5px;
	padding: 11.5px 15px;

	transition: all .2s ease;
	&:focus {
		border-color: $white;
		outline: none;
	}

	&::placeholder {
		color: $grey;
	}

	/* Input text states */
	
	&.error {
		border-color: $red;
		padding-right: 136px;

		& + .msg {
			display: block;
			width: 116px;
			text-align: right;
			color: $red;

			@media (max-width: 480px) {
				width: 100px;
			}
		}

		@media (max-width: 480px) {
			padding-right: 110px;
		}
	}

	&[disabled] {
		cursor: not-allowed;
	}
}
/* Input Text end */

/* Modal */
.modal {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 100;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: rgba(3, 10, 33, 0.95);
	visibility: hidden;
	
	&.active {
		visibility: visible;
	}

	&__content {
		width: 435px;
		background-color: $blue-2;
		border-radius: 5px;
		padding: 25px 35px;

		transform-style: preserve-3d;
		transform: rotateX(-60deg);
		transform-origin: 50% 0;
		opacity: 0;
		transition: all .3s ease;

		&-wrapper {
			position: relative;
			-webkit-perspective: 1300px;
			perspective: 1300px;
		}

		&-wrapper {
			@media (max-width: 480px) {
				width: 100%;
				margin: 0 auto;
			}
		}
	}

	&.active &__content {
		transform: rotateX(0);
		opacity: 1;
	}

	.close-btn {
		position: absolute;
		right: 4px;
		bottom: calc(100% + 2px);
		z-index: 1;
		display: flex;
		align-items: center;
		gap: 11px;
		color: $lblue;

		&::before {
			content: 'Close';
		}

		&::after {
			content: '';
			display: block;
			flex-shrink: 0;
			width: 12px;
			height: 12px;
			background-image: url('data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%2212%22%20height%3D%2212%22%20viewBox%3D%220%200%2012%2012%22%20fill%3D%22none%22%3E%0A%3Cpath%20d%3D%22M12%201.05L10.95%200L6%204.95L1.05%200L0%201.05L4.95%206L0%2010.95L1.05%2012L6%207.05L10.95%2012L12%2010.95L7.05%206L12%201.05Z%22%20fill%3D%22%236988FF%22%2F%3E%0A%3C%2Fsvg%3E');
			background-repeat: no-repeat;
		}
	}

	@media (max-width: 576px) {
		&__content {
			padding: 20px 25px;
			margin: 0 20px;
		}

		&__title {
			font-size: 25px;
			line-height: calc(32.5/25);
			margin-bottom: 20px;
		}
	}

	@media (max-width: 480px) {
		&__content {
			width: 100%;
			padding: 20px;
			margin: 0;
		}
	}

	/* Modal content style */

	&__info-field {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		gap: 50px;
		margin-bottom: 10px;

		&-title {
			max-width: 105px;
			color: $grey;
		}

		&-val {
			font-size: 20px;
			line-height: calc(26/20);
			text-align: right;

			&.highlighted {
				color: $lblue;
			}

			&_minor {
				font-size: 17px;
				line-height: calc(22.1/17);
				color: $grey;
			}
		}
	}

	&__form-wrapper .form-field {
		display: flex;
		flex-direction: column;
		gap: 15px;
		margin-top: 15px;

		.btn, .input-container {
			width: 100%;
		}

		.input-container {
			display: flex;
			gap: 10px;

			.btn_plain {
				width: fit-content;
			}
		}
	}

	&__tip {
		font-size: 14px;
		line-height: calc(18.2/14);
		font-weight: 500;
		color: $llblue;
		margin-top: 15px;
	}

	@media (max-width: 576px) {
		&__steps {
			gap: 24px;
		}
		&__step {
			font-size: 13px;
			line-height: calc(16.9/13);
		}

		&__info-field {
			&-val {
				font-size: 15px;
				line-height: calc(19.5/15);
			}
		}
	}
}
/* Modal end */

/* Logo */
.logo {
	display: inline-block;
	font-family: "Electrolize", sans-serif;
	font-size: 25px;
	line-height: calc(32.5/25);
	font-weight: 400;
	text-decoration: none;
	color: $white;

	@media (max-width: 576px) {
		font-size: 17px;
		line-height: calc(22.1/17);
	}
}
/* Logo end */

/* Radio and checkbox */
.input-container {
	input[type="radio"], input[type="checkbox"] {
		cursor: pointer;
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		z-index: 1;
		appearance: none;

		& + label {
			display: flex;
			align-items: center;

			&::before {
				content: '';
				display: block;
				width: 15px;
				height: 15px;
				background-color: $white;
				border-radius: 50%;
				margin-right: 13px;
			}
			&::after {
				content: '';
				display: block;
				width: 9px;
				height: 9px;
				position: absolute;
				left: 3px;
				top: 6px;

				background-color: $lblue;
				border-radius: 50%;
				transform: scale(0);
				transform-origin: center;
				transition: transform .15s ease-in;
			}
		}

		&:checked + label {
			&::after {
				transform: scale(1);
			}
		}
	}
}

.input-container {
	input[type="checkbox"] {
		& + label {
			&::before {
				width: 20px;
				height: 20px;
				background-color: $grey;
				border-radius: 3px;
				margin-right: 10px;
			}

			&::after {
				content: '';
				left: 4px;
				display: block;
				flex-shrink: 0;
				width: 13px;
				height: 10px;
				background-color: transparent;
				background-image: url('data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%2213%22%20height%3D%2210%22%20viewBox%3D%220%200%2013%2010%22%20fill%3D%22none%22%3E%0A%3Cpath%20d%3D%22M10.9792%200.31882C11.2039%200.112066%2011.5028%20-0.0022356%2011.813%203.31386e-05C12.1232%200.00230188%2012.4203%200.120964%2012.6417%200.33098C12.8631%200.540997%2012.9914%200.825941%2012.9996%201.12569C13.0078%201.42543%2012.8952%201.71653%2012.6856%201.93756L6.32264%209.63046C6.21323%209.74438%206.08117%209.83581%205.93437%209.89927C5.78757%209.96273%205.62904%209.99692%205.46826%209.9998C5.30748%2010.0027%205.14775%209.97418%204.99862%209.91602C4.8495%209.85786%204.71403%209.77122%204.60033%209.66129L0.380675%205.58205C0.263165%205.4762%200.168913%205.34855%200.103542%205.20672C0.0381707%205.06489%200.00301963%204.91178%200.000186137%204.75653C-0.00264735%204.60128%200.0268948%204.44707%200.0870496%204.30309C0.147204%204.15912%200.23674%204.02834%200.350315%203.91854C0.463889%203.80875%200.599176%203.72219%200.748105%203.66404C0.897034%203.60588%201.05655%203.57732%201.21715%203.58006C1.37774%203.5828%201.53612%203.61678%201.68283%203.67998C1.82955%203.74318%201.96159%203.83429%202.07109%203.94789L5.41045%207.17459L10.9489%200.352737C10.9589%200.340868%2010.968%200.329544%2010.9792%200.31882Z%22%20fill%3D%22%236988FF%22%2F%3E%0A%3C%2Fsvg%3E');
				background-repeat: no-repeat;
				background-position: center;
				transform: translateY(15px);
				opacity: 0;
				transition: transform .2s ease, opacity .2s ease;
			}
		}

		&:checked + label::after {
			transform: translateY(0);
			opacity: 1;
		}
	}
}
/* Radio and checkbox end */


/* Pagination */
.pages {
	display: flex;
	align-items: center;
	gap: 7px;

	&-btn {
		svg path {
			stroke: $lblue;
		}

		&[disabled] {
			svg path {
				stroke: $grey;
			}
		}
	}

	&-container {
		display: flex;
		align-items: center;
		gap: 7px;
		margin-top: 15px;

		@media (max-width: 576px) {
			justify-content: center;
		}
	}
}

.page {
	flex: 1;
	min-width: 30px;
	color: $grey;
	background-color: $blue-1;
	border-radius: 5px;
	padding: 5.5px 10.5px;

	&.current {
		color: $white;
	}
}
/* Pagination end */

button[disabled], .input-container input[disabled] {
	cursor: not-allowed;
}


/* Loader */
.loader-container {
  width: 50px;
	height: 50px;
	margin: 20px auto;
}

@keyframes spin {
  0% {
    stroke-dasharray: 1 98;
    stroke-dashoffset: -105;
  }
  50% {
    stroke-dasharray: 80 10;
    stroke-dashoffset: -160;
  }
  100% {
    stroke-dasharray: 1 98;
    stroke-dashoffset: -300;
  }
}

.loader {
  transform-origin: center;
  animation-name: spin;
  animation-duration: 1.2s;
  animation-timing-function: cubic-bezier;
  animation-iteration-count: infinite;

	@media (max-width: 480px) {
		&-container {
			width: 30px;
			height: 30px;
		}

		stroke-width: 5px;
	}
	
}
/* Loader end */

/* Warning */
.warning {
	position: fixed;
	z-index: 101;
	left: 0;
	right: 0;
	bottom: 0;
	font-size: 15px;
	line-height: 1.3;
	font-weight: 400;
	background-color: $blue-2;
	text-align: right;
	white-space: nowrap;
	padding: 10px 20px;

	@media (max-width: 576px) {
		text-align: center;
	}
}
/* Warning end */